* {
    margin: 0;
    padding: 0;
}

html,
body {
    height: 100vh;
    background: rgb(24, 24, 24);
    font-family: Arial, Helvetica, sans-serif;
}

body {
    overflow-x: hidden;
}

.webgl {
    position: absolute;
    top: 0;
    left: 0;
    outline: none;
    mix-blend-mode: exclusion;
}

.container {
    height: 100vh;
    display: grid;
    place-items: center;
    text-align: center;
}

h1 {
    font-size: 8rem;
    color: white;
}

section {
    height: 100vh;
    width: 100vw;
    background: white;
}